.header {
    background: black;
    margin: 0;
    text-align: center;
    padding: 50px;
    color: white;
    position: fixed;
    width: 100%;
}

h1 {
    font-family: Staatliches;
    font-size: 60px;
    letter-spacing: 6px;
    margin: 0;
    text-shadow: darkgoldenrod 2px 2px;
}

h3, h4 {
    font-family: sans-serif;
    font-weight: normal;
    letter-spacing: 1px;
}
.tile {
    background: #ddd;
    margin: 10px;
    padding: 15px;
    height: 300px;
    width: 300px;
    text-align: center;
    font-size: 18px;
    display: inline-block;
    float: left;
}

.tile p {
    margin-top: 10px;
}
#wrapper {
    background: yellow;
}

.container {
    position: relative;
    top: 280px;
    z-index: -1;
}
